*{box-sizing:border-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}ul{list-style:none;padding:0}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{max-width:1600px;min-height:100vh;margin:0 auto;background-color:#f9f9f9;padding:20px;text-align:center}

.cart{display:flex;min-height:560px;max-width:1200px;background-color:#fff}.cart__products_area{flex:0.65 1;height:100%;padding:0 10px}.cart__product{margin-bottom:5px}.cart__payments{flex:0.35 1;height:100vh;padding:20px 5px;border-left:0.5px solid #f5f5f5}.payments__item{border-bottom:0.5px solid #f5f5f5}.payments__buttons{display:flex;flex-direction:column;padding:10px 0}.payments__pay_button{position:relative;margin-bottom:10px;width:auto;width:initial;height:30px;border-radius:25px;background-color:#ff8522;color:#fff;font-size:14px;font-weight:600;border:none;cursor:pointer}.payments__pay_button::before{display:block;position:absolute;left:30px;top:0.25rem;content:' ';background-image:url(/newtone_tt/static/media/pay.90f46705.svg);background-size:cover;height:23px;width:23px}.payments__shop_button{position:relative;margin-bottom:10px;width:auto;width:initial;height:30px;border-radius:25px;background-color:#ff8522;color:#fff;font-size:14px;font-weight:600;border:none;cursor:pointer}.payment{display:flex;justify-content:space-between}.total_payment{display:flex;justify-content:space-between;padding-top:10px;border-bottom:2px solid #000}.details{position:relative;display:flex;justify-content:space-between;align-items:center;padding:0 10px;border-radius:5px;border:0.1px solid #f5f5f5}.details__presentation{display:flex;align-items:center}.details__img{width:50px;height:50px;margin-right:10px}.details__weight{margin-right:10px;font-size:10px}.details__price{margin-right:10px;font-size:10px}.details__price--special{font-size:10px;font-weight:600;color:#f44336}.details__cost{min-width:-webkit-max-content;min-width:-moz-max-content;min-width:max-content}.details__total{font-size:10px;font-weight:700}.details__remove_btn{position:absolute;top:3px;right:3px;width:15px;height:15px;padding:0;font-size:10px;color:#f5f5f5;background-color:#fff;border-radius:50%;border:1px solid #f5f5f5;cursor:pointer;transition:color 0.2s, border-color 0.2s}.details__remove_btn:hover{color:#ff5722;border-color:#ff5722}

.layout{width:100%;height:100%}.layout__header{display:flex;justify-content:space-around;align-items:center}

.header__title{display:inline-block}.header__button{position:relative;width:180px;height:50px;border-radius:25px;background-color:#ff8522;color:#fff;font-size:14px;font-weight:600;border:none;cursor:pointer}.header__button::before{display:block;position:absolute;left:30px;top:1em;content:' ';background-image:url(/newtone_tt/static/media/cart.4f75bc56.svg);background-size:23px 23px;height:23px;width:23px}

.product{display:flex;flex-direction:column;width:320px;height:370px;margin:5px;padding-top:20px;border-radius:5px;background-color:#fff;transition:box-shadow 0.25s cubic-bezier(0.47, 0, 0.75, 0.72),transform 0.25s}.product:hover{box-shadow:inset 2px 2px 2px 0 rgba(255,255,255,0.5),7px 7px 20px 0 rgba(0,0,0,0.1),4px 4px 5px 0 rgba(0,0,0,0.1);transform:scale(1.02)}.product__foto{width:150px;height:150px;margin:0 auto}.product__description{height:100%;padding:20px;display:flex;flex-direction:column;justify-content:space-between}.product__title{position:relative;text-align:left;text-align:initial}.product__title::before{position:absolute;bottom:-10px;content:'';width:28px;height:2px;background-color:#ff8522}.product__weight_units{text-align:left;text-align:initial;margin:0}.product__footer{display:flex;justify-content:space-between;align-items:center}.product__price{position:relative;font-weight:bold;color:#ff8522}.product__price::before{position:absolute;right:-0.5rem;top:-0.5rem;font-size:0.7rem;content:"$"}.product__special_price{color:#ff5722;width:-webkit-max-content;width:-moz-max-content;width:max-content;margin:0 auto;padding:3px;border-radius:5px;font-weight:700}

.weight_quantity{display:flex;justify-content:space-around;align-items:center;padding:1px 10px;width:125px;height:35px;border-radius:25px;background-color:#ff8522}.weight_quantity__minus,.weight_quantity__plus{color:#fff;cursor:pointer}.weight_quantity__quantity_field{display:flex;justify-content:center;align-items:center;height:100%;padding-right:3px;background-color:#fff}.weight_quantity__input{width:40px;border:none;padding:0 8px;font-size:15px;line-height:1;font-weight:700;text-align:center}.weight_quantity__input:focus{outline:none}.weight_quantity__unit{font-size:16px;line-height:1;font-weight:700}

.products{max-width:1200px;margin:0 auto}.products__list{display:flex;flex-wrap:wrap;justify-content:center}

.start_button{margin:auto;width:200px;height:200px;border-radius:25px;background-color:#ff8522;font-size:40px;font-weight:700;color:#fff;border:none;cursor:pointer;transition:box-shadow 0.25s cubic-bezier(0.47, 0, 0.75, 0.72),transform 0.25s}.start_button:hover{box-shadow:inset 2px 2px 2px 0 rgba(255,255,255,0.5),7px 7px 20px 0 rgba(0,0,0,0.1),4px 4px 5px 0 rgba(0,0,0,0.1);transform:scale(1.02)}

